@custom-media --wide-vp (min-width: 1280px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --narrow-vp (320px <= width < 767.9px);
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

.visually-hidden {
  position: absolute;
  transform: scale(0);
}

.page {
  font-family: var(--main-font);
}

.page__body {
  --scrollbar: calc(100vw - 100%);
  margin: 0;
}

.page__centerer {
  --content-indent: calc(20px + (10vw - 32px) / 4.48);
  margin-inline: var(--content-indent) calc(var(--content-indent) - var(--scrollbar));
}
@media (--narrow-vp) {
  .page__centerer {
    --content-indent: calc(20px + (10vw - 32px) / 4.48);
    margin: 0 auto;
  }
}
@media (--middle-vp) {
  .page__centerer {
    --content-indent: 30px;
    margin: 0 auto;
  }
}
@media (--wide-vp) {
  .page__centerer {
    --content-indent: auto;
    max-width: 1220px;
    margin: 0 auto;
  }
}

.page__form-title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #000000;
  text-align: center;
  margin: 0;
}
@media (--middle-vp) {
  .page__form-title {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}
@media (--wide-vp) {
  .page__form-title {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }
}

.button {
  font-family: var(--accent-font);
  width: fit-content;
}
.button--size_wide {
  width: 100%;
}

.header {
  position: relative;
  box-sizing: border-box;
}

.navigation {
  display: flex;
  align-items: center;
  padding: 10px 0 10px 0;
}
@media (--middle-vp) {
  .navigation {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 19px 0 10px 0;
  }
}
@media (--wide-vp) {
  .navigation {
    padding-top: 53px;
  }
}

.navigation__logo-container {
  flex: 1 0 auto;
}

.navigation__list {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  flex-direction: column;
  display: none;
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: 1;
}
@media (--middle-vp) {
  .navigation__list {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    max-width: 440px;
  }
}
@media (--wide-vp) {
  .navigation__list {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    max-width: 478px;
  }
}

.navigation__logo-link {
  display: grid;
  grid-template-columns: 1fr 2fr;
  box-sizing: border-box;
  align-items: center;
  flex-wrap: nowrap;
  text-decoration: none;
}
@media (--middle-vp) and (--wide-vp) {
  .navigation__logo-link {
    display: flex;
    justify-content: space-between;
    max-width: 174px;
  }
}
@media (--wide-vp) {
  .navigation__logo-link {
    display: flex;
    justify-content: space-between;
    max-width: 202px;
  }
}

.navigation__menu-button {
  box-sizing: border-box;
  width: 24px;
  height: 18px;
  padding: 0;
  margin: 0;
  background-color: transparent;
  outline: none;
  border: none;
}
@media (--middle-vp) {
  .navigation__menu-button {
    display: none;
  }
}
@media (--wide-vp) {
  .navigation__menu-button {
    display: none;
  }
}

.navigation__menu-button::after {
  content: url("data:image/svg+xml,%3Csvg width='24' height='18' viewBox='0 0 24 18' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='%23000' stroke-width='1.5' d='M0 1.25h24M0 9.25h24M0 17.25h24'/%3E%3C/svg%3E");
  display: block;
  width: auto;
  height: auto;
  border: none;
  outline: none;
}

.navigation__menu-button:hover + .navigation__list {
  display: block;
}

.navigation__menu-button:hover::after {
  content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath transform='scale(.97098 1.0282) rotate(-45 24.51 7.237)' stroke='%23000' stroke-width='1.5' d='M0-.75h24.758'/%3E%3Cpath transform='matrix(.68659 .72705 -.68659 .72705 1 2)' stroke='%23000' stroke-width='1.5' d='M0-.75h24.758'/%3E%3C/svg%3E");
  display: block;
  width: auto;
  height: auto;
  border: none;
  outline: none;
}

.navigation__item {
  box-sizing: border-box;
  background-color: #ffffff;
  border-top: 1px solid;
  border-color: #e6e6e6;
}
@media (--wide-vp) {
